"use strict";

import "./styles.css";
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
import gsap from "gsap";

const canvas = document.getElementsByClassName("webgl");

const loadingManager = new THREE.LoadingManager();

loadingManager.onStart = () => {
  console.log("loading started");
};

loadingManager.onProgress = () => {
  console.log("progressing...");
};

loadingManager.onLoad = () => {
  console.log("loading completed");
};

loadingManager.onError = () => {
  console.log("everything is glitching............");
};

const textureLoader = new THREE.TextureLoader(loadingManager);

const texture = textureLoader.load("/texture.jpg");

const scene = new THREE.Scene();

const torus = new THREE.Mesh(
  new THREE.TorusGeometry(1, 2, 2),
  new THREE.MeshBasicMaterial({
    color: 0x000000,
    wireframe: true,
    transparent: true,
    opacity: 1,
  })
);

//torus.material.map = texture;

scene.add(torus);

const camera = new THREE.PerspectiveCamera(
  75,
  window.innerWidth / window.innerHeight,
  0.1,
  100
);

camera.position.set(0, 0, 7);

camera.lookAt(torus.position);

//torus.position.x = -8;
//torus.position.y = 5;
//torus.position.z = -5;

const controls = new OrbitControls(camera, canvas[0]);

controls.enableDamping = true;

controls.update();

const renderer = new THREE.WebGLRenderer({ canvas: canvas[0] });

window.addEventListener("resize", () => {
  camera.aspect = window.innerWidth / window.innerHeight;
  camera.updateProjectionMatrix();

  gsap.to();

  renderer.setSize(window.innerWidth, window.innerHeight);
});

window.addEventListener("dblclick", () => {
  if (!document.fullscreenElement) {
    canvas[0].requestFullscreen();
  } else {
    document.exitFullscreen();
  }
});

const clock = new THREE.Clock();

const animate = () => {
  controls.update();

  gsap.to(camera.position, { duration: 8, z: 0.1 });

  renderer.setSize(window.innerWidth, window.innerHeight);
  renderer.setClearColor(0xffffff);
  renderer.render(scene, camera);

  window.requestAnimationFrame(animate);
};

animate();

